<template>
  <!-- 楼层 开始 -->
  <view class="prindexfloor">
    <view class="floor_item">
      <!--个人信息-->
      <view class="floor_person">
        <view class="person_detial">
          <view class="admin_pic">
            <img v-bind:src="list.userHeader" mode="widthFix"></img>
          </view>
          <view class="admin_detial">
            <view class="admin_name">{{list.userName}}</view>
            <view class="admin_time">{{list.postTime}}</view>
          </view>
        </view>
	 </view>
	  
      <view class="floor_ques">{{list.postText}}</view>
	  
	  <view class="floor_pic">
		  <view class="detail_pic" v-if="detail">
			<view class="pic_item" v-for="(item,index) in list.postImages" >
				<img :src="item" mode="widthFix" @click="lookImg(index)"></img>
			</view>
		  </view>
        <img v-for="(item,index) in list.postImages" :src="item" alt="" mode="widthFix" v-else>
      </view>
	  
      <view class="floor_type">
        <text v-for="(item,index) in list.tagList">#{{item.content}}</text>
      </view>
	  
	  
	  <view class="floor_moreOption">
	  	<view class="transmit" @click.stop="transmit">
			<image src="../../../static/icons/square/transmit.png" mode="widthFix"></image>
	  		{{list.sharedNum}}
	  	</view>
		<view class="comment">
			<navigator url="/src/pages/level2/profile/comment">
			<image src="../../../static/icons/square/comment.png" mode="widthFix"></image>
			{{list.commentNum}}</navigator>
		</view>
		<view class="like" @click.stop="likeIt(list.articleId)">
			<image src="../../../static/icons/square/like.png" mode="widthFix" v-if="list.like"></image>
			<image src="../../../static/icons/square/unlike.png" mode="widthFix" v-else></image>
			{{list.likedNum}}
		</view>
	  </view>
    </view>
  </view>
  <!-- 楼层 结束 -->
</template>

<script>
export default {
  name: "PrIndexFloor",
  data() {
    return {
	};
  },
  props: {
    list: {
      type: Object,
      default() {
        return {};
      },
    },
	detail: {
	  type: Boolean,
	  default() {
	    return false;
	  },
	},
  },
  methods:{
	  //点击转发函数
  	  transmit(){
		  //调用本地的分享接口
		  plus.share.sendWithSystem({type:'text',content:'分享内容',href:'http://www.dcloud.io/'}, function(){
		  		console.log('分享成功');
		  	}, function(e){
		  		console.log('分享失败：'+JSON.stringify(e));
		  	});
  	  },
	  //点击更多选项
	  bandleMoreBtn(){
		  this.$emit("bandleMoreBtn");
	  },
    //点赞与取消点赞
    likeIt(postId) {
      if (this.list.like) {
		  this.list.likedNum--;
        this.$emit("unlikeIt", postId);
      } else {
		  this.list.likedNum++;
        this.$emit("likeIt", postId);
      }
       this.list.like = !this.list.like; //状态取反
    },
	//查看图片
	lookImg(val) {
	  const urls = this.list.postImages;
	  const current = val.index;
	  uni.previewImage({
	    urls,
	    current,
	  });
	},
  },
};
</script>
<style scoped>
/* 不进行展示部分东西开始 */
.display_none{
	display: none;
}
/* 不进行展示部分东西结束 */
.prindexfloor {
  

}
.floor_person {
	margin: 20rpx 0 24rpx 0;
	height: 80rpx;
}
.floor_item {
  padding:0 23px 0 16px;
  border-bottom: 12rpx solid #fff0d9;
  padding-bottom: 10px;
}
.person_detial {
  height: 80rpx;
  float: left;
  display: flex;
  align-items: center;
}
.admin_pic {
  flex: 1;
}
.admin_pic img {
  vertical-align: middle;
  box-sizing: border-box;
  width: 80rpx;
  height: 80rpx;
  border: 1px solid #8f8f94;
  border-radius: 80rpx;
}

.admin_detial {
  margin-left: 14px;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.admin_name {
  /* height: 40rpx; */
  width: 150px;
  line-height: 16px;
  display: inline-block;
  font-size: 16px;
  color: black;
}
.admin_time {
  /* height: 40rpx; */
  line-height: 12px;
  display: inline-block;
  font-size: 12px;
  color: darkgray;
}
.other_btn {
  float: right;
  height: 80rpx;
  line-height: 80rpx;
  margin-right: 50rpx;
}

.grade image {
  width: 29px;
  height: 29px;
}

.follow_btn {
  margin-right: 46rpx;
  display: inline-block;
  height: 46rpx;
  width: 98rpx;
  border-radius: 46rpx;
  line-height: 46rpx;
  text-align: center;
  background-color: #ffebc2;
  font-size: 14px;
  font-weight: 400;
}
.more_btn {
  display: inline-block;
}
.more_btn image {
  height: 40rpx;
  margin-bottom: 8rpx;
  vertical-align: middle;
}
.floor_ques {
  padding:0 28px 9px 28px;
  color: #333333;
  font-family: Microsoft YaHei;
  font-size: 14px;
  line-height: normal;
}
.floor_type {
  padding-bottom: 6px;
}
.floor_type text {
  padding: 10rpx 15px 10rpx 15px;
  text-align: center;
  line-height: 28rpx;
  display: inline-block;
  font-size: 12px;
  height: 28rpx;
  border-radius: 40rpx;
  display: inline-block;
  background-color: #ffebc2;
}
.floor_pic {
	text-align: center;
	margin-bottom: 22rpx;
}
.floor_pic img {
  height: 320rpx;
  padding: 3px 5px;
}
.pic_item img {
border-radius: 10px;
  width: 212rpx;
  height: 224rpx;
}
.detail_pic{
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
	width: 456rpx;
	margin: 0 auto;
}
.pic_item{
	width: 212rpx;
	height: 224rpx;
	overflow: hidden;
}
/* 更多操作开始 */
.floor_moreOption{
	margin-top: 10px;
	margin-bottom: 20px;
	display: flex;
	justify-content: space-around;
	align-items: center;
}
.floor_moreOption image{
	padding-right: 5px;
	vertical-align: middle;
	width: 19px;
	height: 17px;
}
.transmit{
	
}
